import {Typography, Paper, Dialog, DialogTitle, DialogContent, DialogActions, Button, Table, TableHead, TableBody, TableRow, TableCell} from '@mui/material';
import {useEffect, useState} from 'react';

const tableRow = ['','','','','','','','','',''];
export default () => {
  const [userInfo, setUserInfo] = useState([]);
  const [toggleDelete, setToggleDelete] = useState(false);
  const callApi = async ()=>{
    const response = await fetch('http://ec2-3-34-106-74.ap-northeast-2.compute.amazonaws.com:5000/lifeConcierge/api');
    const body = await response.json();
    return body;
  }
  useEffect(()=>{
    callApi()
    .then((res)=>{setUserInfo(res)})
    .catch((err)=>{console.log(err)});
  });

  return(
    <Paper sx={{width:"90%", m:"auto", marginTop:"50px"}}>
      <Typography sx={{textAlign:"center", fontSize:"25px", mb:"30px"}}>  </Typography>
      <Table>
        <TableHead>
          <TableRow>
            {tableRow.map(cell=><TableCell key={cell}>{cell}</TableCell>)}
          </TableRow>
        </TableHead>
        <TableBody>
          {userInfo?userInfo.map((user,index)=><TableRow key={index}>
            <TableCell>{user.id}</TableCell>
            <TableCell>{user.pw}</TableCell>
            <TableCell>{user.name}</TableCell>
            <TableCell>{user.gender}</TableCell>
            <TableCell>{user.birthday}</TableCell>
            <TableCell>{user.job}</TableCell>
            <TableCell>{user.hAddr}</TableCell>
            <TableCell>{user.cAddr}</TableCell>
            <TableCell>{user.disease}</TableCell>
            <TableCell><Button variant="contained" color="success" onClick={()=>{setToggleDelete(true)}}></Button></TableCell>
          </TableRow>):" ."}
          <Dialog open={toggleDelete} onClose={()=>{setToggleDelete(false)}}>
            <DialogContent>
              <DialogTitle>
                <Typography>   ?</Typography>
              </DialogTitle>
              <DialogActions>
                <Button variant="contained" color="success" onClick={()=>{setToggleDelete(false)}}></Button>
                <Button variant="outlined" color="success" onClick={()=>{setToggleDelete(false)}}></Button>
              </DialogActions>
            </DialogContent>
          </Dialog>

        </TableBody>
      </Table>
    </Paper>
  );
}